import React, { useState } from 'react';
import './css/login.css';
import Axios from 'axios';

let Login = () => {
    const BASEURL = 'http://localhost:2000';
    let [foto, setFoto] = useState('https://i.stack.imgur.com/qwf7L.png');
    let [matricula, setMatricula] = useState('');
    let [classButton, setclassButton] = useState('button is-link has-text-centered is-medium');
    let [activeInputs, setActiveInputs] = useState(false);
    let [logged, setLogged] = useState(false);
    let [dados, setDados] = useState([]);
    let [materiaisAp, setMateriais] = useState([]);
    let [indexOut, setIndexOut] = useState(0);
    let [materialFiltrado, setMaterialFiltrado] = useState([]);

    let loginOnFoa = async evt => {
      evt.preventDefault();
      let form = evt.target;
      let matricula = form.querySelector('input[type="matricula"]').value;
      let senha = form.querySelector('input[type="password"]').value;
      setclassButton(classButton = 'button is-link has-text-centered is-medium is-loading');
      setActiveInputs(true);
      try{
        let dadosResponse = await Axios.post(`${BASEURL}/data`,{matricula,senha});
        if(dadosResponse){
          setDados(dados = dadosResponse.data);
          setLogged(true);
          await setMateriais(materiaisAp = dados.map((elem,i) =>elem.materaisDeApoio));
          let filtro = await materiaisAp.filter( (elem,indexIn) =>  indexOut === indexIn);
          await setMaterialFiltrado(materialFiltrado = filtro);
        }
      }catch(err){
        console.log(err)
        setclassButton(classButton = 'button is-link has-text-centered is-medium');
        setActiveInputs(false);
        alert('Usurio ou senha incorreto.')
      }
    }

    let closeModal = () => document.querySelector('div.modal.is-active').setAttribute('class','modal');

    let downloadAll = () => {
      if(materialFiltrado.length > 0){
          let array = (materialFiltrado[0]).map( a => (a.apoio[0].url['$link']['href']));
          array.forEach( link => window.open(link,'_blank','width=300,height=300'));
      }
    }

    let renderModal = (elem,index) => {
      if(elem){
        return(
              <tbody key={index}>
                <tr>
                  <th>{`${new Date(elem.data).getDate()}/${new Date(elem.data).getMonth()}/${new Date(elem.data).getFullYear()}`}</th>
                  <td>{elem.assunto}</td>
                  {(elem.apoio).map((item,indexT) =><td key={indexT}><a href={item['url']['$link']['href']} target="_blank" download="arquivo">Download</a></td>)}
                </tr>
              </tbody>
        )
      }else{
        return ( <div key={index}>
          <p>No foi encontrado nenhum material de apoio</p>
        </div>)
      }
    }

    window.onclick = function(event) {
      if(event.target === document.querySelector('div.modal-background')){
        closeModal();
      }
    }

    let materialShow = num =>{
      setIndexOut(indexOut = num);
      let filtro = materiaisAp.filter( (elem,indexIn) =>  indexOut === indexIn);
      setMaterialFiltrado(materialFiltrado = filtro);
      document.querySelector('div.modal').setAttribute('class','modal is-active');
    }

    let renderHoje = (elem,index) =>{
      if(elem.diaSemana === new Date().getDay()){
        return(<div className="column is-one-third" key={index}>
        <div className="card large arred">
          {new Date().getDay() === elem.diaSemana ? <div className="card-header subTitle-header-edit">
            <span className="card-header-title is-centered subTitleStyle">(Aula Hoje!)</span>
          </div> : <div className="card-header subTitle-header-edit">
              <span className="card-header-title is-centered subTitleStyle2"></span>
          </div> }
          <div className="card-header title-header-edit">
              <span className="card-header-title is-centered titleStyle">{`${elem.codMateria} - ${elem.materia}`}</span>
          </div>
          <div className="card-content">
            <div className="columns is-mobile">
              <div className="column to-center">
                <span className="bd-notification is-primary falta-style">{`${elem.suasFaltas || 0 } Faltas / ${elem.limiteFaltas} Faltas`}</span>
              </div>
            </div>
          </div>
          <div className="content along-content">
            <div className="columns is-mobile">
              <div className="column to-center organize">
                <span className="bd-notification is-primary inside-content"> 
                  { elem.horarioInicio ? <div><p>Horrio de Incio:{ elem.horarioInicio}</p><p>Horrio do Trmino: { elem.horarioFim } </p></div> : <p>Nenhum Horrio Encontrado</p> }
                </span>
              </div>
            </div>
          </div>
          <div className="columns is-mobile">
            <div className="column to-center organize">
              <button className="button is-link is-small is-info" onClick={() => materialShow(index)} >Consultar Material</button>
            </div>
          </div>
          <div className="card-footer to-center footer-style">
            <span className="text-white font-weight-bold mg-0">Nenhuma nota encontrada</span>
          </div>
          </div>
        </div>)
      }
    }

    let renderOtherDays = (elem,index) => {
        if(elem.diaSemana !== new Date().getDay()){
          return (<div className="column is-one-third" key={index}>
          <div className="card large arred">
            {new Date().getDay() === elem.diaSemana ? <div className="card-header subTitle-header-edit">
            <span className="card-header-title is-centered subTitleStyle">(Aula Hoje!)</span>
            </div> : <div className="card-header subTitle-header-edit">
                <span className="card-header-title is-centered subTitleStyle2"></span>
            </div> }
            <div className="card-header title-header-edit">
                <span className="card-header-title is-centered titleStyle">{`${elem.codMateria} - ${elem.materia}`}</span>
            </div>
            <div className="card-content">
              <div className="columns is-mobile">
                <div className="column to-center">
                  <span className="bd-notification is-primary falta-style">{`${elem.suasFaltas || 0 } Faltas / ${elem.limiteFaltas} Faltas`}</span>
                </div>
              </div>
            </div>
            <div className="content along-content">
              <div className="columns is-mobile">
                <div className="column to-center organize">
                  <span className="bd-notification is-primary inside-content"> 
                  { elem.horarioInicio ? <div><p>Horrio de Incio:{ elem.horarioInicio}</p><p>Horrio do Trmino: { elem.horarioFim } </p></div> : <p>Nenhum Horrio Encontrado</p> }
                  </span>
                </div>
              </div>
            </div>
            <div className="columns is-mobile">
              <div className="column to-center organize">
                <button className="button is-link is-small is-info" onClick={() => materialShow(index)} >Consultar Material</button>
              </div>
            </div>
            <div className="card-footer to-center footer-style">
              <span className="text-white font-weight-bold mg-0">Nenhuma nota encontrada</span>
            </div>
            </div>
          </div>)
        }
    }

    let buscaFoto = async evt =>{
      setMatricula(matricula = evt.target.value);
      matricula.length === 9 ? setFoto(foto=`https://sites.unifoa.edu.br/portal/FotosDRA/${matricula}.jpg`) : setFoto(foto=`https://i.stack.imgur.com/qwf7L.png`);
    }
    if(!logged){
        return (
          <section className="hero is-success is-fullheight">
            <div className="hero-body">
                <div className="container has-text-centered">
                    <div className="column is-4 is-offset-4 up-container">
                        <div className="box">
                            <figure className="avatar">
                                <img className="avatarConfig" alt="Avatar Login" src={ foto } onError={()=> setFoto(foto=`https://i.stack.imgur.com/qwf7L.png`)} />
                            </figure>
                            <h3 className="login-text">Login</h3>
                            <form onSubmit={loginOnFoa}>
                                <div className="field">
                                    <div className="control">
                                        <input className="input is-medium" type="matricula" placeholder="Matrcula" value={matricula} onChange={buscaFoto} disabled={activeInputs} />
                                    </div>
                                </div>
                                {matricula.length === 9 && foto !== `https://i.stack.imgur.com/qwf7L.png` ? (
                                  <div className="field slide-top">
                                      <div className="control">
                                          <input className="input is-medium" type="password" placeholder="Senha" disabled={activeInputs} />
                                      </div>
                                  </div>) : ""
                                }
                                {matricula.length === 9 && foto !== `https://i.stack.imgur.com/qwf7L.png` ? (
                                  <div className="columns is-mobile">
                                    <div className="column is-4 is-offset-4">
                                      <button className={classButton}>Entrar</button>
                                    </div>
                                  </div>) : (
                                  <div className="columns is-mobile">
                                    <div className="column is-4 is-offset-4">
                                      <button className={classButton} disabled>Entrar</button>
                                    </div>
                                  </div>)
                                }
                            </form>
                        </div>
                    </div>
                </div>
            </div>
        </section>
      );
    }else{
      return(
        <section>
          <div className="modal">
            <div className="modal-background"></div>
            <div className="modal-card">
              <header className="modal-card-head">
                <p className="modal-card-title">Material de Apoio</p>
                <button className="delete" aria-label="close" onClick={closeModal}></button>
              </header>
              <section className="modal-card-body">
              <table className="table is-fullwidth">
                <thead>
                  <tr>
                    <th><abbr title="Data">Data</abbr></th>
                    <th><abbr title="Descrio">Descrio</abbr></th>
                    <th><abbr title="Anexos">Anexos</abbr></th>
                  </tr>
                </thead>
                <tfoot>
                  <tr>
                    <th><abbr title="Data">Data</abbr></th>
                    <th><abbr title="Descrio">Descrio</abbr></th>
                    <th><abbr title="Anexos">Anexos</abbr></th>
                  </tr>
                </tfoot>
                {materialFiltrado.length > 0 ? materialFiltrado[0].map(renderModal) : materialFiltrado.map(renderModal) }
              </table>
              </section>
              <footer className="modal-card-foot">
              <button className="button is-link is-small is-info" onClick={() => downloadAll()} >Baixar todos Anexos</button>
              </footer>
            </div>
          </div>

          <div className="container">
            <div className="section">
              <div className="row columns is-multiline">
              {dados.map(renderHoje)}
              {dados.map(renderOtherDays)}
                </div>
              </div>
            </div>
          <footer className="footer resize-m">
            <div className="container">
              <div className="content has-text-centered">
                <p>
                  <strong>Developed</strong> by  <a href="https://facebook.com/fcsms">Frederico Sampaio</a>.
                  The source code is licensed <a href="http://opensource.org/licenses/mit-license.php">MIT</a>. <br/>
                </p>
              </div>
            </div>
          </footer>
        </section>
      );
    }
}

export default Login;



