import React from 'react';
import ReactDOM from 'react-dom';
import Login from './Pages/Login';
import { BrowserRouter, Switch, Route } from 'react-router-dom';

ReactDOM.render(
	<BrowserRouter>
		<Switch>
			<Route path="/" component={Login} />
		</Switch>
	</BrowserRouter>,
	document.getElementById('root')
);



